
































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { Item } from "@/structs/Item";

@Component
export default class CreateDialog extends Vue {
  alphabet: string[] = "abcdefghijklmnopqrstuvwxyz1234567890".split("");
  dialog: boolean = false;
  item: any = {
    title: null,
    keys: null
  };

  reset() {
    this.item.title = null;
    this.item.keys = null;
      this.dialog = false;
  }
  submit() {
    
      this.$emit("item", Object.assign({}, this.item));
      this.reset()
    
  }
}
