<template>
  <v-app>
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
        <span>ma</span>
        <span class="font-weight-light">LINKA</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <create-dialog @item="createCategory">категорию</create-dialog>
      <create-dialog @item="createStatement">фразу</create-dialog>
      <v-btn @click="fetchAudio">Обновить аудио-файлы</v-btn>
    </v-toolbar>
    <v-content>
      <v-container grid-list-md text-xs-center>
        <v-layout row wrap class="list-container">
          <v-flex xs6>
            <categories-list ref="cls" @choose="setCategory" />
          </v-flex>
          <v-flex xs6>
            <v-card>
              <statements-list ref="sls" @choose="setStatement" :category="currentCategory" />
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
    <edit-footer :category="currentCategory" :statement="currentStatement" @edited="onEdited" />
  </v-app>
</template>

<script>
import CategoriesList from "./components/CategoriesList";
import StatementsList from "./components/StatementsList";
import EditFooter from "./components/EditFooter";
import CreateDialog from "./components/CreateDialog";
import Axios from "axios";
import { Category } from "./structs/Category";
import { Statement } from "./structs/Statement";

export default {
  name: "App",
  components: {
    CategoriesList,
    StatementsList,
    EditFooter,
    CreateDialog
  },
  data() {
    return {
      currentCategory: null,
      currentStatement: null
    };
  },
  methods: {
    setCategory(category) {
      this.currentCategory = category;
    },

    setStatement(statement) {
      this.currentStatement = statement;
    },
    onEdited() {
      this.currentStatement == null;
      this.$refs.sls.update();
    },
    fetchAudio() {
      Axios.get("//malinka/fetch-audio");
    },
    async createCategory(raw) {
      const category = new Category(null, raw.title, raw.keys);
      await Axios.post("//malinka/create/category", category);
      this.onEdited();
    },
    async createStatement(raw) {
      const statement = new Statement(null, raw.title, raw.keys, [
        this.currentCategory.id
      ]);
      await Axios.post("//malinka/create/statement", statement);
      this.onEdited();
    }
  }
};
</script>


